/* colors */
/* elements height */
.footer-main-row, .main-row, .main-container, #root, html, body {
  width: 100%;
  margin: 0;
  max-width: 100vw;
}

#root, html, body {
  padding: 0;
  min-height: 100vh;
}

h1 {
  font-size: 1.875rem;
}

a {
  color: #262626;
}
a:hover {
  color: #e9b563;
}

p {
  margin: 0.3125rem;
  padding: 0.625rem;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4.375rem;
}

#root {
  font-family: "Quicksand", "Arial", sans-serif;
  font-size: 0.875rem;
  position: relative;
}

.main-container {
  padding: 0 5vw;
}

.main-row {
  min-height: 100vh;
  padding: 0 0 4.375rem 0;
}

.row-height {
  height: calc((40rem - 4.375rem - 1.25rem)/ 3 - 1.25rem);
}

.row-m-p {
  margin: 0 0 0.625rem 0;
  padding: 0.3125rem;
}

.col-m-p {
  margin: 0.625rem;
  padding: 0.3125rem;
}

.image-size {
  height: auto;
  max-width: 100%;
  max-height: calc(calc((40rem - 4.375rem - 1.25rem)/ 3 - 1.25rem) - 0.625rem);
}

.dark-bg {
  background-color: #262626;
}

.gray-bg {
  background-color: #666666;
}

.blue-bg {
  background-color: #d4e7f7;
}

.beige-bg {
  background-color: #fce6cf;
}

.gold-bg {
  background-color: #e9b563;
}

.relative {
  position: relative;
}

.opacity-08 {
  opacity: 0.8;
}

@media (max-width: 768px) {
  footer {
    height: 8.75rem;
  }

  .main-row {
    padding: 0 0 8.75rem 0;
  }

  .col-m-p {
    margin: 0.625rem 0;
    padding: 0.3125rem;
  }

  .row-m-p {
    margin: 0.625rem;
    padding: 0.3125rem;
  }
  .row-m-p.xs {
    margin: 0;
    padding: 0;
  }

  .row-height.xs-h {
    height: 100%;
  }
}
footer .main-container {
  height: 100%;
}

.footer-main-row {
  padding: 0;
  height: 100%;
}

.footer-nav-link {
  margin: 0 0.625rem 0 0;
  padding: 0.3125rem 0.3125rem 0.3125rem 0;
  color: white;
}
.footer-nav-link:hover {
  text-decoration: underline;
}

.footer-icon {
  font-size: 1rem;
  color: white;
}

